// @flow

import styled from 'styled-components/macro';
import {words2minutes, toPostDate} from '../../utilities';
import {colors} from '../../config/styles';

export default function Meta({
    author,
    date,
    words
}: {
    author: string,
    date: Date,
    words: number
}) {
    return (
        <StyledMeta>
            By {author} on {toPostDate(date)} &bull; {words2minutes(words)} min read
        </StyledMeta>
    );
}

export const StyledMeta = styled.small`
    font-size: 0.8rem;
    line-height: 1.2;
    ${colors.withVar('dimgray', 'color')};
`;
